    height: 100%;

    .character-container {
      flex-direction: column-reverse;

      justify-content: flex-end;
      margin-top: 100px;
      // height: 100%;
      // margin-bottom: 100px;

      .character-container-content {
        // margin-top: 0;
        // height: auto;
        margin-top: 0;
        // max-height: auto;

        .selected-container {
          display: flex;
          flex-direction: column;
          height: auto;
          // top: 0;
          // top: 27%;
          // top: calc(100vh - 80%);
