      // width: 60px;

      // min-width: 60px;
      // max-width: 80px;
      // min-height: 60px;
      // max-height: 120px;

      height: 100%;
      max-height: 120px;
      // min-height: 60px;
      // max-height: 80px;
      border-radius: 50%;

      // padding: 30px;
      background-color: white;
    }
  }
}
